@import '../vars';
$title-offset: 50%;
$hover-offset: 1em;

.article-title {

    width: 100%;
    height: 10em;
    margin-bottom: 1em;

    position: relative;

    &:hover .title-text {
        left: calc(#{$title-offset} + #{$hover-offset});
    }

    &:hover .title-image {
        left: -$hover-offset;
        .title-text {
            left: calc(#{$title-offset} + #{$hover-offset * 2})
        }
    }


    .title-image {
        position: absolute;
        top: 0;
        left: 0;

        transition: left 0.2s;

        z-index: 1;

        width: 100%;
        height: 10em;

        mask-image: linear-gradient(135deg, rgba(255,255,255,1), rgba(255,255,255,1) 49.85%, rgba(255,255,255,0) 50%);
        background-color: $cd2-white;

        user-select: none;
        overflow: hidden;
        
        img {
            width: 100%;
        }

        .title-text {
            color: $cd2-black;
        }
    }

    .title-text {
        position: absolute;
        transform: translateX(-50%) translateY(-50%);
        left: $title-offset;
        top: 5em;
        width: 90%;
        text-align: center;

        transition: left 0.2s;
        h1 {
            font-size: 3.5em;
        }

    }

    .title-image-overlay {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        width: 70%;
        background: linear-gradient(30deg,rgba(57,20,103,0.4) 2%, rgba(45,33,223,0.4) 20%, rgba(43,199,233,0.4) 52%, rgba(34,238,212,0.4) 65%, rgba(48,249,202,0.4) 85%, rgba(35,255,145,0.4) 100%);

    }
    
}