@import '../vars';

$height: 20em;

.content-container {
    margin-top: 2em;
    margin-bottom: 10em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.content {
    position: relative;
    flex: 1 1 auto;
    
    max-width: 90%;

    display: flex;
    flex-direction: column;
    align-items: center;
    

    p {    
        @media only screen and (max-width: 600px) {
            font-size: 1em !important;
        }
        text-align: justify;
    }
    h1 {
        font-size: 3em;
    }
}

