@import '../vars';


.header-container {
    position: relative;

    overflow: hidden;
    max-width: 100vw;
    width: 100vw;

    height: 5.5em;
    
    h1 {
        position: absolute;
        left: 1em;
        a {
            text-decoration: none;
            color: $cd2-white;
            user-select: none;
        }
    }
}

.header {
    width: 16em;    
    height: 100%;
    background: linear-gradient(210deg, rgba(57,20,103,1) 2%, rgba(45,33,223,1) 20%, rgba(43,199,233,1) 52%, rgba(34,238,212,1) 65%, rgba(48,249,202,1) 85%, rgba(35,255,145,1) 100%);
    mask-image: linear-gradient(135deg, rgba(255,255,255,1), rgba(255,255,255,1) 49.9%, rgba(255,255,255,0) 50%);

    
    h1 {
        a {
            color: $cd2-black;
        }
    }
}