
@import "vars";

html, body, #root {
    width: 100vw;
    margin: 0;
    overflow-x: hidden;
}

h1 {
    font-family: Inter, sans-serif;
    font-weight: bolder;
    letter-spacing: -0.04em;
    font-size: 2.25em;
}
h2 {
    font-family: Inter, sans-serif;
    font-weight: 300;
    font-size: 2em;
}
h3 {
    font-family: Inter, sans-serif;
    font-weight: 300;
    font-size: 1.75em;
}
h4 {
    font-family: Inter, sans-serif;
    font-weight: 300;
    font-size: 1.5em;
}
h5 {
    font-family: Inter, sans-serif;
    font-weight: 700;
    font-size: 1.25em;
} 
h6 {
    font-family: Inter, sans-serif;
    font-weight: 700;
    font-size: 1em;
    text-transform: uppercase;
}

p {
    font-family: Nunito, sans-serif;
    font-size: 1.25em
}

img {
    max-width: 100%;
    display: block;
}

html {

    color: $cd2-white;
    background-color: $cd2-black;
    
    a:link {
        color: $cd-blue-300
    }
    a:visited {
        color: $cd-blue-600
    }
    a:hover, a:active {
        color: $cd-blue-200
    }

    ::selection {
        background-color: $cd-green-200;
        color: $cd2-black;
    }
}