@import '../vars';

.headercontainer {
    h1 {
        
        @media only screen and (max-width: 600px) {
            font-size: 5rem;
            top: 10rem;
        }
        @media only screen and (min-width: 600px) {
            font-size: 10rem;
            top: 20rem;
        }
        position: absolute;
        left: 50%;
        margin-top: 0;
        transform: translateX(-50%) translateY(-50%);

        user-select: none;
    }

}

.headerimage {    
    background: linear-gradient(210deg,rgba(57,20,103,1) 2%, rgba(45,33,223,1) 20%, rgba(43,199,233,1) 52%, rgba(34,238,212,1) 65%, rgba(48,249,202,1) 85%, rgba(35,255,145,1) 100%);
    mask-image: linear-gradient(135deg, rgba(255,255,255,1), rgba(255,255,255,1) 49.95%, rgba(255,255,255,0) 50%);
    
    @media only screen and (max-width: 600px) {
        height: 20rem;
    }
    @media only screen and (min-width: 600px) {
        height: 40rem;
    }
}

.fronttitle {
    color: $cd2-black;
}
.backtitle {
    color: $cd2-white;
}

.contentcontainer {
    margin-top: 2em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.content {
    position: relative;
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    p {
        max-width: 90vw;
        width: 40rem;
    }

    &>p {

        &:last-of-type {
            margin-bottom: 3em;
        }
    }
}